import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { IncreaseIndent as Button } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import { increaseIndent } from '../actions';
import { INCREASE_HOT_KEY_TIP } from '../utils/hotKeyTips';
;

export interface IncreaseIndentButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function IncreaseIndentButton(props: IncreaseIndentButtonProps) {
  const { controller, locale, ...rest } = props;
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge || controller.query('isSelectionInHr') || controller.query('isSelectionInListSymbol');

  const handleClick = React.useCallback(() => controller.run('onAction', increaseIndent()), [controller]);

  return (
    <Button
      {...rest}
      shortcutText={INCREASE_HOT_KEY_TIP}
      testid="bi-toolbar-incindent"
      onClick={handleClick}
      disabled={!!isDisabled}
      role={PluginRoles.indentIncrease}
    />
  );
}
